import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			tabCurrentIndex: 0,
			totalPages: 1,
			currentpage: 1,
			refresh: true,
			reflash: false,
			orderList: [],
			navList: [{
					state: 0,
					text: '全部',
					orderList: []
				},
				{
					state: 1,
					text: '待付款',
					orderList: []
				},
				{
					state: 2,
					text: '待发货',
					orderList: []
				},
				{
					state: 3,
					text: '待收货',
					orderList: []
				},
				{
					state: 4,
					text: '待评价',
					orderList: []
				}
				// {
				// 	state: 4,
				// 	text: '售后',
				// 	loadingType: 'more',
				// 	orderList: []
				// }
			],
		};
	},
	onLoad(options){
		this.tabCurrentIndex = options.state;
		// console.log(this.tabCurrentIndex)
		this.loadData()
	},
	onShow(){
		this.loadData()
	},
	onPullDownRefresh() {
		this.currentpage = 1
		this.refresh = true;
		this.loadData(true);
	},
	onReachBottom() {
		if(this.currentpage >= this.totalPages) return false
		this.currentpage++;
		this.loadData()
	},
	methods: { 
		navTo(id) {
			uni.navigateTo({
				url: `/pages/order/orderDetail?id=${id}`
			})
		},
		//获取订单列表
		loadData(flag){
			uni.showLoading({title: '正在加载'})
			let orderurl = '/order/list?showType='+ this.tabCurrentIndex +'&page='+this.currentpage+'&limit=10'
			api.httpget(orderurl).then((res) => {
				let respons = res.data
				if (respons.errno == 0) {
					if(this.refresh) this.orderList = []
					this.orderList = this.orderList.concat(respons.data.list)
					this.totalPages = respons.data.pages
					//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
					// this.$set(navItem, 'loaded', true);
					//判断是否还有数据， 有改为 more， 没有改为noMore 
					// navItem.loadingType = 'more';
					this.refresh = false
				} else {
					uni.showToast({title: respons.errmsg, icon: 'none'})
				}
				if(flag) uni.stopPullDownRefresh()
				uni.hideLoading()
			})
		}, 
		//顶部tab点击
		tabClick(index){
			this.refresh = true
			this.tabCurrentIndex = index;
			this.currentpage = 1;
			this.loadData()
		},
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				let {stateTip, stateTipColor} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip, 
					stateTipColor
				})
				
				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val=>val.id === item.id);
				index !== -1 && list.splice(index, 1);
				
				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
}
